.starting-page_mainWrapper {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: rgba(227, 227, 227, 0.95);
  display: grid;
  place-content: center;

  .starting-page_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2rem;
    max-width: 900px;
    span {
      line-height: 1.6;
      font-size: 1rem;
      white-space: pre-wrap;
      word-wrap: break-word;
    }
    .effective-altruism {
      font-size: 1.2rem;
      transition: 1s;
      font-weight: bold;
      &:hover {
        scale: 120%;
      }
    }
  }

  h3 {
    filter: blur(.5px);
    color: #b2b0b0;
  }
}